.App {
  text-align: center;
	min-height: 100vh;
	font-family: Arial, Helvetica, sans-serif;
}

.banner_container {
	width: 100%;
	height: auto;
	/* display: flex; */
	/* justify-content: center; */
}

.banner {
	width: 90%;
}

/* .icons_wrapper {
	display: flex;
	position: fixed;
	width: 4%;
} */

.tadLink {
	position: absolute;
	left: 2%;
	/* top: 50px; */
}

.tadLink > img {
	height: 50px;
	position: fixed;
	/* top: 8%; */
	/* top: -25px; */
	cursor: pointer;

	top: 90px;
}

.tadLink > img:hover {
	filter: brightness(0.5);
	-webkit-filter: brightness(0.5);
}

.yt_container {
	background-color: lightblue;
	position: fixed;
	width: 55%;
	height: 55vh;
	opacity: 0;
	transform: scale(0.1);
	top: 0;
	/* transform: translateY(-50%); */
	left: -2000px;
}

.yt_chat_container {
	background-color: lightblue;
	position: fixed;
	width: 31%;
	height: 55vh;
	opacity: 0;
	transform: scale(0.1);
	top: 0;
	/* transform: translateY(-50%); */
	right: -2000px;
}

.show_yt_chat {
	right: 6%;
	transform: scale(1);
	opacity: 1;
	transition: right 0.5s ease-out, transform 0.5s ease-out, opacity 0.5s ease-out;
}

.hide_yt_chat {
	right: -1000px;
	transform: scale(0.1);
	opacity: 0;
	transition: right 0.5s ease-out, transform 0.5s ease-out, opacity 0.5s ease-out;
}

.show_yt {
	left: 7%;
	transform: scale(1);
	opacity: 1;
	transition: left 0.5s ease-out, transform 0.5s ease-out, opacity 0.5s ease-out;
}

.hide_yt {
	left: -1000px;
	transform: scale(0.1);
	opacity: 0;
	transition: left 0.5s ease-out, transform 0.5s ease-out, opacity 0.5s ease-out;
}

.yt_video {
	width: 100%;
	height: 100%;
}

.chat_embed {
	width: 100%;
	height: 100%;
}

.banner_icon {
	height: 50px;
	position: fixed;
	/* top: 8%; */
	/* top: -25px; */
	cursor: pointer;
	/* align-self: center; */
}

.yt_logo_icon {
	left: 2%;
	top: 30px;
}

.chat_yt_logo {
	top: 60px;
}

.banner_icon:hover {
	filter: brightness(0.5);
	-webkit-filter: brightness(0.5);
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.dark_theme {
  background-color: #282c34;
  color: white;
}

.light_theme {
  background-color: #eee;
  color: #282c34;
}

.App-link {
  color: #61dafb;
}

.input_container {
  background-color: #eee;
  display: flex;
}

.input_dark_container {
  background-color: #282c34;
}

.myInput {
  display: block;
  padding: 10px 0;
  font-size: 20px;
  margin-bottom: 10px;
  border-radius: 10px;
  outline: none;
  padding-left: 10px;
  width: calc(100% - 10px - 4px);
}

.myInput:focus {
  background-color: rgb(234, 250, 255);
}

.myInput_dark_theme {
  background-color: #505050;
  color: #fff;
  border-color: #303030;
}

.myInput_dark_theme:focus {
  background-color: #606060;
}

.page-container {
  max-width: 1000px;
  min-width: 200px;
	padding-bottom: 10px;
	margin: 0 auto;
	margin-top: -5px;
}

.arrow-up {
  position: fixed;
  bottom: 20px; 
  right: 20px;
  font-size: 2em;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50px;
  background-color: #ddd;
  color: #303030;
}

.arrow-up-dark-theme {
  background-color: #404040;
  color: #fff;
}

.arrow-up:hover {
  background-color: #ccc;
  cursor: pointer;
}

.arrow-up-dark-theme:hover {
  background-color: #505050;
}

.switchPageButtonsContainerDark {
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.switchPageButtonsContainerLight {
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.switchPageButtonsContainerDark > button {
  padding: 5px;
  flex: 1;
  box-sizing: border-box;
  margin: 0 10px;
  border: none;
  outline: none;
  background-color: #505050;
  color: #fff;
}

.switchPageButtonsContainerLight > button {
  padding: 5px;
  flex: 1;
  box-sizing: border-box;
  margin: 0 10px;
  border: none;
  outline: none;
  background-color: #ddd;
  color: #000;
}

.switchPageButtonsContainerLight > button:hover {
  background-color: #bbb;
  color: #000;
}

.switchPageButtonsContainerDark > button:hover {
  background-color: #404040;
  color: #fff;
}

.switchPageButtonsContainerDark > button.isActiveButtonDark {
  background-color: #21de7c;
  color: #000;
}

.switchPageButtonsContainerLight > button.isActiveButtonLight {
  background-color: #21de7c;
  color: #000;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 768px) {
  .myInput {
    font-size: 16px;
	}

	.chat_yt_logo, .yt_logo_icon, .tadLink {
		display: none;
	}
	
	.banner {
		width: 100%;
	}

  .arrow-up {
    bottom: 10px; 
    right: 10px;
    font-size: 1.5em;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 40px;
    background-color: rgba(221, 221, 221, 0.6);
  }

  .arrow-up:hover {
    background-color: rgba(204, 204, 204, 0.6);
  }

  .arrow-up-dark-theme {
    background-color: rgba(64, 64, 64, 0.6);
    color: #fff;
  }

  .arrow-up-dark-theme:hover {
    background-color: #30303099;
  }
}
